/* eslint-disable */
import React from "react"
import {useDispatch, useSelector} from "react-redux";
import StationInfo from "./StationInfo";
import {useParams} from "react-router-dom"
import {fetchStations} from "../redux/actions/fetch-stations";
import {connectWss} from "../redux/actions/connect-wss";
import {descStation} from "../util/getDescription"
import {setCurStation} from "../util/setCurStation";

function StationInfoContainer(props) {
    const dispatch = useDispatch();
    const {items, loading, openWss, current} = useSelector(({stations}) => stations);

    const {id} = useParams()
    const getInfo = (item) => {

        const {name, id, description} = item.station;
        const {is_live, streamer_name} = item.live;
        const {elapsed, duration, song} = item.now_playing;
        const {
            art, artist, title, album,
        } = song;
        return {
            name,
            id,
            description: descStation(artist, title, description, streamer_name, is_live),
            art, album, is_live,
            streamer_name, elapsed, duration,
        }
    }

    const selectStation = (event, item) => {
        event.stopPropagation()
        if (event.key === ' ' || event.type === 'click') {
            setCurStation(item, dispatch, current)
        }
    };

    React.useEffect(() => {
        if (loading) {
            dispatch(fetchStations());
        } else if (!openWss) {
            dispatch(connectWss(items));
        }
    }, [items]);
    return (
        <>
            {items.map(item => {
                if (item.station.id === +id) {
                    return (
                        <StationInfo selectStation={() => selectStation(event, item)} active={id == current.id}
                                     stationInfo={getInfo(item)}/>
                    )
                }
            })
            }
        </>
    )

}

export default StationInfoContainer