/* eslint-disable */
export const descStation = (artist, song, desc, streamer, isLive) => {
    let string;

    if (desc.includes('live') === true || isLive) {
        string = streamer;
    } else if (artist) {
        string = `${song} (${artist})`;
    } else {
        string = song;
    }
    return string;
};