/* eslint-disable */
import React from 'react';
import {useDispatch, useSelector} from 'react-redux';

import {MAIN_HOST} from '../data';
import {Station} from '../components';
import {connectWss} from '../redux/actions/connect-wss';
import {fetchStations} from '../redux/actions/fetch-stations';
import {descStation} from "../util/getDescription";
import {setCurStation} from "../util/setCurStation";
import {getMediaSession} from "../util/MediaSession";

function Main() {
    const dispatch = useDispatch();
    getMediaSession()
    const [active, setActive] = React.useState(false)
    const {
        items, loading, current, openWss,
    } = useSelector(
        ({stations}) => stations,
    );
    const selectStation = (event, item) => {
        if (event.key === ' ' || event.type === 'click') {
            setCurStation(item, dispatch, current)
        }
    };
    React.useEffect(() => {
        if (loading) {
            dispatch(fetchStations());
        } else if (!openWss) {
            dispatch(connectWss(items));
        }
    }, [items]);

    return (
        <div className="container container--offset_top container--offset_left">
            <div className="stations">
                {items.map((item) => {
                    const {name, id, description} = item.station;
                    const {is_live, streamer_name} = item.live;
                    const {elapsed, duration, song} = item.now_playing;
                    const {
                        art, artist, title, album,
                    } = song;

                    const isShow = window.location.hostname === MAIN_HOST
                        ? description.includes('disable') !== true
                        : true;

                    return (
                        isShow && (
                            <Station
                                key={id}
                                id={id}
                                name={name}
                                status={description}
                                description={descStation(artist, title, description, streamer_name, is_live)}
                                isLive={is_live}
                                elapsed={elapsed}
                                duration={duration}
                                art={art}
                                album={album}
                                active={id === current.id}
                                onClick={() => selectStation(event, item)}
                            />
                        )
                    );
                })}
            </div>
        </div>
    );
}

export default Main;
