/* eslint-disable */
import React from "react";
import {Timer} from "../components/StationBlock";
import {useHistory} from "react-router-dom"
import share from "../assets/download/Vector.png"
import bg from '../assets/download/detail_bg.png'
import playButton from '../assets/download/detail_play.png'
import classNames from 'classnames';


const StationInfo = ({stationInfo, active, selectStation}) => {
    const history = useHistory()
    const {
        name,
        id,
        description,
        art,
        album,
        duration,
        elapsed,
    } = stationInfo
    const goBack = () => {
        history.push('/')
    }
    const copyLink = () => {
        navigator.clipboard.writeText(album).then(r => alert("Ссылка скопирована в буфер обмена"))
    }
    const imageBg = (image) => (image.includes('200x200') ? image.replace('200x200', '1920x1080') : `/stations-ico/1920x1080_${id}.webp`);
    return (
        <div className="station-info">
            <img className="station-info__img" src={bg} alt={description}/>
            <button
                type="button"
                className="close-button popup__close station-info__close"
                onClick={goBack}
            >
                <svg
                    className="close-button__icon"
                    fill="#fff"
                    width={19}
                    height={19}
                    viewBox="0 0 19 19"
                    xmlns="http://www.w3.org/2000/svg"
                >
                    <path
                        d="M11.2407 9.50014L18.6389 2.10154C19.1204 1.6203 19.1204 0.842184 18.6389 0.360936C18.1577 -0.120312 17.3796 -0.120312 16.8983 0.360936L9.49989 7.75953L2.10167 0.360936C1.62021 -0.120312 0.842336 -0.120312 0.361098 0.360936C-0.120366 0.842184 -0.120366 1.6203 0.361098 2.10154L7.75932 9.50014L0.361098 16.8987C-0.120366 17.38 -0.120366 18.1581 0.361098 18.6393C0.600928 18.8794 0.916268 19 1.23138 19C1.5465 19 1.86161 18.8794 2.10167 18.6393L9.49989 11.2407L16.8983 18.6393C17.1384 18.8794 17.4535 19 17.7686 19C18.0837 19 18.3988 18.8794 18.6389 18.6393C19.1204 18.1581 19.1204 17.38 18.6389 16.8987L11.2407 9.50014Z"/>
                </svg>
            </button>
            <div className='station-info__container container container--offset_top '>
                <div className="description station-info__description">
                    <h2 className="description-title">Scratch Radio</h2>
                    <div className="description-text">
                        This can be demonstrated with the following experiment: hold a tennis racket at its handle, with
                        its face being horizontal, and try to throw it in the air
                    </div>
                </div>
                <div className={"station station-item--current"}>
                    <div className="station_title">Now Playing</div>
                    <div className="station-share"><img onClick={copyLink} src={share} alt="share"/></div>
                    <div className="station-inner">
                        <div><img onClick={event => selectStation(event)}
                                  className="station-inner__img image"
                                  src={art} alt={description}/></div>
                        <div><img onClick={event => selectStation(event)}
                                  className={classNames({
                                      "image_active": !active,
                                      "image_disable":active
                                  })}
                                  src={playButton} alt={description}/></div>
                        <div className="station-inner__description">
                            <div className="station-inner__name">{description}</div>
                            <div className="station-inner__d">
                                {album && <a href={album} className="station-inner__link">Link to artist</a>}
                                <Timer duration={duration} elapsed={elapsed}
                                       className="station-item__timer station-inner_timer"/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default StationInfo;
