/* eslint-disable */

import {descStation} from "./getDescription";
import {useSelector} from "react-redux";


export const getMediaSession = () => {
    const {
        items, current
    } = useSelector(({stations}) => stations);

    if ("mediaSession" in navigator) {
        items.forEach(item => {
            if (item.station.id === current.id) {
                const {description} = item.station;
                const {is_live, streamer_name} = item.live;
                const {song} = item.now_playing;
                const {artist, title, art} = song;

                navigator.mediaSession.metadata = new MediaMetadata({
                    title: descStation(artist, title, description, streamer_name, is_live),
                    artist: artist,
                    album: item.station.name,
                    artwork: [
                        {src: 'https://via.placeholder.com/96', sizes: '96x96', type: 'image/png'},
                        {src: 'https://via.placeholder.com/128', sizes: '128x128', type: 'image/png'},
                        {src: 'https://via.placeholder.com/192', sizes: '192x192', type: 'image/png'},
                        {src: 'https://via.placeholder.com/256', sizes: '256x256', type: 'image/png'},
                        {src: 'https://via.placeholder.com/384', sizes: '384x384', type: 'image/png'},
                        {src: {art}, sizes: '512x512', type: 'image/jpg'},
                    ]
                });
            }
        })
    }
}